@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body{width:100vw;height:100vh;overflow:hidden;font-size:1.5rem;color:#aaa;background-color:#ddd;font-family:'Poppins', sans-serif}body .container{width:100vw;margin:0 auto;display:block;margin-top:20vh;text-align:center}body .container #time-left,body .container #timer-label{color:#aaa}body .container #timer-label{top:175px;font-size:2rem}body .container .lenght-container{margin-top:1em;text-align:center;display:inline-block}body .container .lenght-container .length{font-size:.9em}body .container .start_stop_reset_container{margin-top:25px}body .container .start_stop_reset_container #start_stop{cursor:pointer}body .container p{color:#5e5e5e}body .container button{border:none;margin:0 10px;cursor:pointer;color:#5e5e5e}body .container button button:hover{box-shadow:1px 2px 0 0.5px #d0d0d0}body .container #reset{color:#f7f7f7}body .container footer{left:0;right:0;bottom:10px;margin:0 auto;font-size:.55em;position:absolute}body .container footer a{color:darkgreen;text-decoration:none}body .container footer a:hover{color:orangered}

